/* eslint-disable */
const React = require('react')
const Uppy = require('@uppy/core')
const Tus = require('@uppy/tus')
const GoogleDrive = require('@uppy/google-drive')
const { Dashboard, DashboardModal, DragDrop, ProgressBar } = require('@uppy/react')
import { Icon, Input, AutoComplete } from 'antd';
import 'antd/dist/antd.css';
import Map from './Map';

const { Option, OptGroup } = AutoComplete;

const dataSource = [
  {
    title: 'Kinder Morgan',
    children: [
      {
        title: 'Gulf Coast Express',
        id: '278867.0000.0000'
      }
    ]
  },
  {
    title: 'New York State Energy Research',
    children: [
      {
        title: 'NYSERDA Multifamily',
        id: '114810.0000.0000',
      }
    ]
  },
  {
    title: 'West Texas Swogp',
    children: [
      {
        title: 'West Texas Swogp',
        id: '117781.0000.0000',
      },
    ],
  },
];

function renderTitle(title) {
  return (
    <span>
      {title}
      <a
        style={{ float: 'right' }}
        href="#"
        target="_blank"
        rel="noopener noreferrer"
      >
        more
      </a>
    </span>
  );
}

const options = dataSource
  .map(group => (
    <OptGroup key={group.title} label={renderTitle(group.title)}>
      {group.children.map(opt => (
        <Option key={opt.title} value={opt.title}>
          {opt.title}
          <span className="certain-search-item-count">{opt.id}</span>
        </Option>
      ))}
    </OptGroup>
  ))
  .concat([
    <Option disabled key="all" className="show-all">
      <a href="#" target="_blank" rel="noopener noreferrer">
        View all projects
      </a>
    </Option>,
  ]);



class Upload extends React.Component {
  constructor (props) {
    super(props)

    this.state = {
      showInlineDashboard: false,
      open: false,
      uploaded: false,
      showMap: false
    }

    this.uppy = new Uppy({ id: 'uppy1', autoProceed: true, debug: true,
      restrictions: {
        maxFileSize: 10000000,
        maxNumberOfFiles: 1,
        minNumberOfFiles: 1
      }
     })
      .use(Tus, { endpoint: 'https://master.tus.io/files/' })
      .use(GoogleDrive, { companionUrl: 'https://companion.uppy.io' })

    this.uppy2 = new Uppy({ id: 'uppy2', autoProceed: false, debug: true })
      .use(Tus, { endpoint: 'https://master.tus.io/files/' })

    this.handleModalClick = this.handleModalClick.bind(this)
    this.showMap = this.showMap.bind(this);
    this.uppy.on('complete', (result) => {
        this.setState({
          uploaded: true
        })
        console.log('successful files:', result.successful)
        console.log('failed files:', result.failed)
      })
  }

  componentWillUnmount () {
    this.uppy.close()
    this.uppy2.close()
  }

  handleModalClick () {
    this.setState({
      open: !this.state.open
    })
  }

  showMap() {
    this.setState({
      showMap: true
    })
    console.log('loading map ...')

  }
  render () {
    const { showInlineDashboard, showMap, uploaded } = this.state
    let theMap = <span/>;
    let theUploadArea = <div>
        <h2>Shapefile Upload</h2>
        <h3>Drag Drop Area</h3>
        <DragDrop
          uppy={this.uppy}
          plugins={['GoogleDrive', 'Dropbox']}
          locale={{
            strings: {
              chooseFile: 'Boop a file',
              orDragDrop: 'or yoink it here'
            }
          }}
        />
        <ProgressBar
          uppy={this.uppy}
          hideAfterFinish={false}
        />
        </div>;


    if (showMap) {
      theMap = <Map />;
      theUploadArea = <span />;
    }
    let theButton = <span/>;
    if (uploaded) {
      theButton = <span onClick={this.showMap} id='editshape'>View Your Shape</span>
    }
    return (
    <div>
     <div className="certain-category-search-wrapper">
      <AutoComplete
        className="certain-category-search"
        dropdownClassName="certain-category-search-dropdown"
        dropdownMatchSelectWidth={false}
        dropdownStyle={{ width: 300 }}
        size="large"
        style={{ width: '100%' }}
        dataSource={options}
        placeholder="select projects"
        optionLabelProp="value"
      >
        <Input suffix={<Icon type="search" className="certain-category-icon" />} />
      </AutoComplete>
    </div>
      <div className="mainUpload">
        {theUploadArea}
        {theButton}      
      </div>
      <div>
        {theMap}
      </div>
      </div>
    )
  }
}


export default Upload;