const ProtectedUser = () =>  <div className="loggedIn"><Header /><div className="loggedInContent"><Basic /></div></div>;
const ProtectedUserPost = () =>  <div className="loggedIn"><Header /><div className="loggedInContent"><BasicPost /></div></div>;

const ProtectedUpload = () =>  <div className="loggedIn"><Header /><div className="loggedInContent"><Upload /></div></div>;
const ProtectedMap = () =>  <div className="loggedIn"><Header /><div className="loggedInContent"><Map /></div></div>;
const ProtectedEQT = () =>  <div className="loggedIn"><Header /><div className="loggedInContent"><BasicPostEQT /></div></div>;
const ProtectedWrappedRealTime = () =>  <div className="loggedIn"><Header /><div className="loggedInContent"><WrappedRealTime /></div></div>;
const ProtectedWrappedRWE = () =>  <div className="loggedIn"><Header /><div className="loggedInContent"><RWE /></div></div>;


class Login extends React.Component {
  constructor(props) {
    super(props);
    localStorage.removeItem('trclogin');

    this.state = {
      name: null,
      redirectToReferrer: false
    };
  }

  handleInputChange = (event) => {
    const target = event.target;
    const value = target.type === 'checkbox' ? target.checked : target.value;
    const name = target.name;
    localStorage.setItem('trclogin', name);
    this.setState({
      [name]: value
    });
  }

  login = () => {
    const TEST_COMBO = this.state.tempname + "," + this.state.temppass;
    const index = POSSIBLE_TEST_COMBO.indexOf(TEST_COMBO);
    if (index > -1){
      fakeAuthCentralState.authenticate(() => {
        this.setState(() => ({
          redirectToReferrer: true
        }));
      });
      message.success("Hi, "+ TESTING_USERS[index].username);
    }
    else {
      message.warning("Incorrect username or password");
    }
  }

  render() {
    const { from } = this.props.location.state || { from: { pathname: '/protecteddata-rwe' } };
    const { redirectToReferrer } = this.state;

    if (redirectToReferrer === true) {
      this.props.history.push(from.pathname);
    }

    return (
         <div>
         <Header />
          <div className="login">
               <div className="login-form">
                <div className="control-group">
                <input type="text" className="login-field" placeholder="username" id="login-name" name="tempname" onChange={this.handleInputChange}/>
                <label className="login-field-icon fui-user" for="login-name"></label>
                </div>

                <div className="control-group">
                <input type="password" className="login-field" placeholder="password" id="login-pass" name="temppass" onChange={this.handleInputChange}/>
                <label className="login-field-icon fui-lock" for="login-pass"></label>
                </div>

                <div className="control-group">
                <input type="checkbox" name="remember" value="true" className= "remember" name="tempremember" onChange={this.handleInputChange} /> 
                <label className="login-field-icon remember" for="login-remember">
                Remember me</label>
                </div>

                <button className="btn btn-primary btn-large btn-block" onClick={this.login}>login</button>
              </div>
            </div>
      </div>
    )
  }
}


const ProtectedRoute = ({ component: Component, ...rest }) => (
  <Route {...rest} render={(props) => (
    fakeAuthCentralState.isAuthenticated ? <Component {...props} />
      : <Redirect to={{
          pathname: '/userlogin',
          state: { from: props.location }
        }} />
  )} />
);

function logOut() {
    let body = document.querySelector('body');
    body.classList.add('logOut');
}

const AuthButton = withRouter(({ history }) => (
  fakeAuthCentralState.isAuthenticated ? (
      <a href="/logout" onClick={() => {
        fakeAuthCentralState.signout(() => history.push('/'));
      }}>Log Out</a>
  ) : (
    <a href="/userlogin">Log In</a>
  )
));

class App extends Component {
  render() {
    return (
      <div className="App" style={{ width: "100%", height: "100%" }}>
        <BrowserRouter>
          <div>
            <Route path="/" exact component={withRouter(Login)}/>
            <ProtectedRoute path='/upload' component={ProtectedUpload} />
            <ProtectedRoute path='/map' component={ProtectedMap} />
            <ProtectedRoute path='/protecteddata-long-wkt' component={ProtectedUserPost} />
            <ProtectedRoute path='/protecteddata-long-wkt-eqt' component={ProtectedEQT} />
            <ProtectedRoute path='/protecteddata-rwe' component={ProtectedWrappedRWE} />
            <ProtectedRoute path='/protecteddata-realtime' component={ProtectedWrappedRealTime} />
            <Route path="/userlogin" component={withRouter(Login)}/>
            <Route path="/logout" component={withRouter(LogOut)}/>      
          </div>
      </BrowserRouter>
      </div>
    );
  }
}

export default App;
