   ajaxCall(url, callback = this.parseDemo) {
      let xhttp = new XMLHttpRequest();
      xhttp.onreadystatechange = function() {
        if (this.readyState === 4 && this.status == 200) {
          callback(this);
        }
      };
      xhttp.open("GET",url);
      //test ajax call with POST
      xhttp.send();
    }

    ajaxCallPOST(url, callback) {
        var data = new FormData();
        data.append("datasource", "SS.Prop.ParcelDetail/ParcelDetail");
        data.append("inclusiongeometries", wkt2);
        data.append("SS_CANDY", demoCandy)
        data.append("fields", QUERY_FIELDS);

        var xhr = new XMLHttpRequest();
        xhr.withCredentials = true;

        xhr.addEventListener("readystatechange", function () {
          if (this.readyState === 4 && this.status == 200) {
            callback(this);
          }
        });

        xhr.open("POST", url);
        xhr.setRequestHeader("Content-Type", "application/x-www-form-urlencoded");
        xhr.send(data);
    }
    removeSign(arr) {
        let _arr = [];
        let _col = [];
        //remove $ in data
        let imageCount = arr.length;
        let percentage = 0;
        let imageAvailableCount = 0;
        let priceInfo = 0;
        const UNIT_PRICE = 6;
        if (imageCount > 0){
            arr.map(function(i){
            //if there's a missing field,show 'unavailable'
            let imageInfo = '-';
            if (i["$"]['SITE_ZIP'] && i["$"]['SITE_CITY'] && i["$"]['SITE_STATE'] && i["$"]['PROP_ADDRESS']){
                /////////////IF IMAGE IS AVAILABLE//////////////
                imageAvailableCount = imageAvailableCount + 1;
                imageInfo = getImageAvailable + '&dmpId='+ i["$"]['_DMP_ID'] + '&city=' + i["$"]['SITE_CITY'] + '&state=' + i["$"]['SITE_STATE'] + '&zip='+ i["$"]['SITE_ZIP'] + '&code=THD2&SS_CANDY=' + demoCandy  + '&address=' + i["$"]['PROP_ADDRESS']+ '&obsId=json&obsSuccessMethod=success&obsErrorMethod=error';
                priceInfo = UNIT_PRICE;
            }
            else if (i["$"]['SITE_ZIP'] && i["$"]['COUNTY'] && i["$"]['SITE_STATE'] && i["$"]['APN']){
                /////////////IF IMAGE IS AVAILABLE//////////////
                imageAvailableCount = imageAvailableCount + 1;
                imageInfo = getImageAvailable + '&dmpId='+ i["$"]['_DMP_ID'] + '&code=THD2&SS_CANDY=' + demoCandy + '&apn=' + i["$"]['APN'] + '&county=' + i["$"]['COUNTY'] + '&state=' + i["$"]['SITE_STATE'] + '&zip='+ i["$"]['SITE_ZIP'] + '&obsId=json&obsSuccessMethod=success&obsErrorMethod=error';
                priceInfo = UNIT_PRICE;
            }
            else {
                priceInfo = 0;
            }
            i["$"]["image"] = imageInfo;
            i["$"]["price"] = priceInfo; //unit price
            _arr.push(i["$"]);
        });

        //column header4
        _col = Object.keys(_arr[0]).map((i)=>{
             if (i==="image"){
                return {headerName: i,field: i,cellRenderer:function(params){
                    if (params.value === '-'){
                        return '-'
                    }
                    return '<a href="'+params.value+'" target="_blank"> Image Link </a>'
                }}
             }
             return {headerName: i,field: i,filter: "agTextColumnFilter"}
         });

        //update percentage
        percentage = (imageAvailableCount * 1.0/imageCount * 100).toFixed(2) + '%';
        this.setState({imageAvailablePercentage: percentage, totalPrice: (imageAvailableCount * UNIT_PRICE).toFixed(2)});
        }
        return [_arr,_col];
    }
    parseDemo(xml) {
      let xml2jsString = require('xml2js').parseString;
      const xmlResponse = xml.responseText;
      xml2jsString(xmlResponse,function(err,result){
         
         if (result.Response.Error) {
            console.dir(result);
            console.log("loading cache ... use cache if needed");
            this.ajaxCall("https://raw.githubusercontent.com/kyangTRC/sample/master/python_gen_data/cache-full.xml");
         }
         else {
            let __arr = this.removeSign(result.Response.Results[0].Data[0].Row);
            this.setState({rowData: __arr[0],columnDefs: __arr[1]});
         }
         
      }.bind(this));

    }

