import React, { Component } from 'react'
import { Map as LeafletMap, TileLayer, GeoJSON } from './reactleaflet'
import P3_Data from './P3_DMP.json'
import P3_Data_Route from './P3_ROUTE.json'

type State = {
  lat: number,
  lng: number,
  zoom: number
}

export default class geoJSON extends Component<{}, State> {
  state = {
    lat: 31.00,
    lng: -100.00,
    zoom: 10
  }

  geoJSONStyle(feature) {
    let theColor = '#ff7878';
    let theFillColor = '#ff8989';
    console.log(feature.properties)
    if ((!!feature.properties.MAIL_ZIP && !!feature.properties.MAIL_CITY && !!feature.properties.MAIL_STATE && !!feature.properties.PROP_ADDR) || (!!feature.properties.MAIL_ZIP && !!feature.properties.COUNTY_NAME && !!feature.properties.MAIL_STATE && !!feature.properties.PARCEL_APN)){
      theColor = '#6eff00';
      theFillColor = '#4ad8c3';
    }
    
    return {
      color: theColor,
      weight: 1,
      fillOpacity: 0.5,
      fillColor: theFillColor,
    }
  }
  geoJSONStyleRoute() {
    return {
      color: '#0e4dec',
      weight: 1,
      fillOpacity: 0.5,
      fillColor: '#63c0e1',
    }
  } 
   onEachFeature(feature: Object, layer: Object)  {
    const popupContent = `PARCEL_APN:` + feature.properties.PARCEL_APN;  
  }

  render() {
    const position = [this.state.lat, this.state.lng]
    return (
      <div className="map-container">
      <LeafletMap center={position} zoom={this.state.zoom}>
        <TileLayer
          attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        />
        <GeoJSON
          data={P3_Data}
          style={this.geoJSONStyle}
          onEachFeature={this.onEachFeature}
        />
         <GeoJSON
          data={P3_Data_Route}
          style={this.geoJSONStyleRoute}
        />
      </LeafletMap>
      </div>
    )
  }
}