import {
  Alert,
  Form,
  Select,
  InputNumber,
  Input,
  AutoComplete,
  Button,
  Upload,
  Icon,
  Drawer,
  message
} from 'antd';
import React, { Component, Fragment } from 'react';
import { Map as LeafletMap, TileLayer, GeoJSON, Popup } from './reactleaflet';
import RealTimeData from './RealTimeData';
import Iframe from 'react-iframe'

const { Option, OptGroup } = AutoComplete;
const emptyRoute = {type: "MultiLineString", coordinates:[[]]};
var parse = require('wellknown');
 

const dataSource = [
  {
    title: 'Kinder Morgan',
    children: [
      {
        title: 'Gulf Coast Express',
        id: '278867.0000.0000'
      }
    ]
  },
  {
    title: 'New York State Energy Research',
    children: [
      {
        title: 'NYSERDA Multifamily',
        id: '114810.0000.0000',
      }
    ]
  },
  {
    title: 'West Texas Swogp',
    children: [
      {
        title: 'West Texas Swogp',
        id: '117781.0000.0000',
      },
    ],
  }
];

function renderTitle(title) {
  return (
    <span>
      {title}
      <a
        style={{ float: 'right' }}
        href="#"
        target="_blank"
        rel="noopener noreferrer"
      >
        more
      </a>
    </span>
  );
}

const iconStyle = { fontSize: '24px', color: '#1890ff' };

const options = dataSource
  .map(group => (
    <OptGroup key={group.title} label={renderTitle(group.title)}>
      {group.children.map(opt => (
        <Option key={opt.title} value={opt.title}>
          {opt.title}
          <span className="certain-search-item-count">{opt.id}</span>
        </Option>
      ))}
    </OptGroup>
  ))
  .concat([
    <Option disabled key="all" className="show-all">
      <a href="#" target="_blank" rel="noopener noreferrer">
        View all projects
      </a>
    </Option>,
  ]);



export default class RealTime extends React.Component {
  constructor(props) {
      super(props);
      this.handleUpload = this.handleUpload.bind(this);
      this.state = { 
          visible: false, 
          DataRoute: emptyRoute,     
          lat: 31.00,
          lng: -100.00,
          zoom: 10,
          datavisible: false,
          token_valid: false
        };
    }

  showData = () => {
    this.setState({
      datavisible: true
    });
  }
  showHideDrawer = () => {
    this.setState({
      visible: !this.state.visible,
    });
  };

  onClose = () => {
    this.setState({
      visible: false,
    });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
      else {
        this.setState({datavisible: false});
      }  
    });
  };

  handleUpload = ({file, onSuccess}) => {      
        if (file) {
          let data = new FormData();
          data.append('file', file);
          // this.setState({DataRoute: data})
          // console.log(data);
        }
  }
  normFile = e => {
    console.log('Upload event:', e);
    if (Array.isArray(e)) {
      return e;
    }
    return e && e.fileList;
  };

  getProjectName = (value) => {
    console.log(value);
  }

  beforeUploadWKTParser = (file) => {
                  const reader = new FileReader();
                  this.setState({DataRoute: emptyRoute});
                  reader.onload = e => {
                      const routeData = parse(e.target.result);
                      this.setState({DataRoute: routeData});
                      console.log("routeData");
                      console.log(routeData);
                      if (routeData && routeData.coordinates){
                        this.setState({lat: routeData.coordinates[0][0][1], lng: routeData.coordinates[0][0][0]});
                      }
                  };
                  reader.readAsText(file);

                  // Prevent upload, to enable, use customRequest={this.handleUpload}
                  return false;
              }
  geoJSONStyleRoute() {
    return {
      color: '#0e4dec',
      weight: 4,
      fillOpacity: 0.5,
      fillColor: '#63c0e1',
    }
  } 

  render() {
    const { getFieldDecorator } = this.props.form;
    const position = [this.state.lat, this.state.lng];

    let realTimeData = <span />;
    let formData = <Fragment>
        <Form.Item label="Choose Document Type(s)">
          {getFieldDecorator('select-multiple', {
            rules: [
              { required: true, message: 'Please select the document type(s)', type: 'array' },
            ],
          })(
            <Select mode="multiple" placeholder="Please select the document type(s)">
              <Option value="deed">Deed Documents</Option>
              <Option value="transaction-history">Transaction History</Option>
              <Option value="property-detail">Property Detail</Option>
              <Option value="all-documents">All Documents Available</Option>
            </Select>,
          )}
        </Form.Item>
        <Form.Item label="Upload Shapefile and View it on Map">
          <Iframe url="http://ec2-54-80-67-31.compute-1.amazonaws.com/map/"
            width="1000px"
            height="400px"
            id="myMap"
            className="mapFrame"
            display="initial"
            position="relative"/>
         </Form.Item>
        <Form.Item label="Buffer Distance">
          {getFieldDecorator('input-number', { initialValue: 150 })(<InputNumber min={1} max={1000} />)}
          <span className="ant-form-text"> feet</span>
        </Form.Item>


        <Form.Item wrapperCol={{ span: 12, offset: 6 }}>
          <Button type="primary" htmlType="submit" onClick={this.showData}>
             Process Shapefile
          </Button>
        </Form.Item>
        </Fragment>


    if (this.state.datavisible) {
      realTimeData = <RealTimeData />;
      formData = <span />
    } 
    // else if (this.state.datavisible && !this.state.token_valid) {
    //   realTimeData =  <Alert message="Authorization token expired, please contact the admin" type="error" />
    // }

    const formItemLayout = {
      labelCol: { span: 6 },
      wrapperCol: { span: 14 },
    };


    return (
      <Form {...formItemLayout} onSubmit={this.handleSubmit}>
        <Form.Item label={<span><Icon type="cloud-upload" style={iconStyle}/><Icon type="api"  style={iconStyle}/><Icon type="file-search" style={iconStyle}/></span>}>
          <span className="ant-form-text">Real-time Shapefile Upload and Processing</span>
        </Form.Item>
        <Form.Item label="Select or Create A Project" hasFeedback>
          {getFieldDecorator('select', {
            rules: [{ required: true, message: 'Please select or create a project' }],
          })(
              <AutoComplete
                className="certain-category-search"
                dropdownClassName="certain-category-search-dropdown"
                dropdownMatchSelectWidth={false}
                dropdownStyle={{ width: 300 }}
                size="large"
                style={{ width: '100%' }}
                dataSource={options}
                placeholder="select projects"
                optionLabelProp="value"
                onChange = {this.getProjectName}
              >
                <Input/>
              </AutoComplete>,
          )}
        </Form.Item>
        {formData}
        {realTimeData}
      </Form>
    );
  }
}

export const WrappedRealTime = Form.create({ name: 'validate_other' })(RealTime);